.wp-block-button .wp-block-button__link {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out, filter .15s ease-in-out;
  background: transparent;
  font-family: "Nunito Sans", sans-serif !important;
  background-color: #000000;
  margin: 5px;
}

.wp-block-button .wp-block-button__link:hover {
  filter: brightness(90%);
}

.wp-block-button.is-style-default .wp-block-button__link {
  border-radius: 0.25rem;
}

.wp-block-button.is-style-outline .wp-block-button__link, .is-style-outline .wp-block-button__link {
  border: 2px solid currentcolor !important;
  background-color: transparent !important;
}

.wp-block-button.is-style-squared .wp-block-button__link {
  border-radius: 0rem;
}
